@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
header {
  display: flex;
  height: 80px;
  align-items: center;
  background: #ee4035;
  position: fixed;
  top: 0;
  width: 100%;
}

.container-space {
  flex: 1 1;
}

.pokebola {
  margin-left: 5px;
  width: 65px;
}

.search-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  width: 470px;
  height: 39px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
  margin-right: 5px;
}

.close-icon {
  border: 1px solid transparent;
  background-color: transparent;
  display: inline-block;
  vertical-align: middle;
  outline: 0;
  cursor: pointer;
}
.close-icon {
  width: 15px;
  height: 15px;
  background-color: #fa9595;
  border-radius: 50%;
  text-align: center;
  color: white;
  font-weight: normal;
  font-size: 12px;
  box-shadow: 0 0 2px #e50f0f;
  cursor: pointer;
}

.search-bar input {
  padding: 2px 4px;
  margin-left: 5px;
  flex: 1 1;
  font-size: 15px;
  width: 100%;
  background: #ffffff;
  border: none;
}

.search-bar form {
  width: 100%;
}

::-webkit-input-placeholder {
  color: #c4c4c4;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #c4c4c4;
  opacity: 1;
}

::-ms-input-placeholder {
  color: #c4c4c4;
  opacity: 1;
}

::placeholder {
  color: #c4c4c4;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #c4c4c4;
}

::-ms-input-placeholder {
  color: #c4c4c4;
}

.container-body {
  padding-top: 80px;
}

.container-list {
  max-width: 960px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(185px, 1fr));
  grid-gap: 10px;
  flex-wrap: wrap;
  margin: 10px auto 30px;
}

.card-pokemon {
  display: flex;
  flex-direction: column;
  margin: 0 auto 30px;
  width: 180px;
  height: 264px;
  background: #fff;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.25);
  border-radius: 7px;
  /* transition: all 100ms ease-out; */
}

.card-pokemon:hover {
  /* transform: scale(1.03); */
  cursor: pointer;
}

.container-image {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 175px;
  height: 170px;
  padding: 7px;
}

.card-pokemon img {
  max-width: 100%;
  max-height: 100%;
}

.card-info {
  display: flex;
  flex-direction: column;
  flex: 2 1;
  background: #c0c0c0;
  padding: 15px;
  -webkit-border-bottom-right-radius: 7px;
  -webkit-border-bottom-left-radius: 7px;
  -moz-border-radius-bottomright: 7px;
  -moz-border-radius-bottomleft: 7px;
  border-bottom-right-radius: 7px;
  border-bottom-left-radius: 7px;
}
.name-pokemon {
  color: #fff;
  font-weight: 500;
  margin-bottom: 5px;
  font-size: 18px;
}

.pokemon-type {
  display: flex;
}

.type-badge {
  width: 70px;
  height: 20px;
  border-radius: 10px;
  margin-right: 10px;
  background: #ff4768;
  align-items: center;
  justify-content: center;
  display: flex;
  color: #fff;
  font-size: 13px;
}

.container-load {
  display: flex;
  justify-content: center;
}

.load-spinner {
  background-color: transparent;
  height: 40px;
  width: 40px;
  -webkit-animation: spin 500ms ease-in-out infinite;
          animation: spin 500ms ease-in-out infinite;
  margin-top: 10px;
}

@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card-pokemon-details {
  display: flex;
  flex-direction: column;
  margin: 0 auto 30px;
  width: 40vw;
  height: 555px;
  background: #fff;
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.25);
  border-radius: 7px;
  margin-top: 20px;
}

@media (max-width: 700px) {
  .card-pokemon-details {
    width: 80vw;
  }
}

.card-pokemon-details .container-image {
  display: flex;
  flex: 2 1;
  justify-content: center;
  width: 100%;
  padding: 10px;
}

.card-pokemon-details img {
  position: relative;
  max-height: 100%;
  max-width: 100%;
}

.card-pokemon-details .card-info {
  display: flex;
  flex-direction: column;
  flex: 3 1;
  background: #a3a3a3;
  padding: 15px;
  -webkit-border-bottom-right-radius: 7px;
  -webkit-border-bottom-left-radius: 7px;
  -moz-border-radius-bottomright: 7px;
  -moz-border-radius-bottomleft: 7px;
  border-bottom-right-radius: 7px;
  border-bottom-left-radius: 7px;
}
.card-pokemon-details .name-pokemon {
  color: #fff;
  font-weight: 500;
  margin-bottom: 10px;
  font-size: 25px;
}

.card-pokemon-details .pokemon-type {
  margin-bottom: 10px;
}

.card-pokemon-details .ability {
  display: flex;
  flex-direction: column;
  width: 100px;
  height: 50px;
  border-radius: 10px;
  margin-right: 10px;
  border: 1px solid #f2f2f2;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 14px;
  text-align: center;
}

.card-pokemon-details .ability p {
  font-size: 10px;
}

.card-pokemon-details .evolutions {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  grid-gap: 10px;
  flex-wrap: wrap;
  margin: 20px auto 30px;
  width: 100%;
}

.card-pokemon-details .evolution-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto 30px;
  width: 100px;
  height: 100px;
  background: #fff;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.25);
  border-radius: 7px;
  transition: all 100ms ease-out;
}

.card-pokemon-details .evolution-card:hover {
  cursor: pointer;
  transform: scale(1.03);
}

.card-pokemon-details .evolution-card .teste {
  width: 90px;
  height: 70px;
}

.card-pokemon-details .evolution-card img {
  max-width: 100%;
  max-height: 100%;
  position: relative;
}

.pokemon-attacks {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  margin-bottom: 10px;
}

#pokemon-name {
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  background: #fff;
  -webkit-font-smoothing: antialiased;
}

body,
input,
button {
  font-family: Roboto, sans-serif;
}

